import { ComponentChildren, createContext } from 'preact';
import { useState, useContext, useEffect } from 'preact/hooks';
import { getCountries } from '@/checkout/api';
import { Country as CountryModel } from '@/checkout/data/models/country';

type Context = { countries?: CountryModel[] };

type CountriesProps = {
  children: ComponentChildren;
};

const CountriesContext = createContext<Context | undefined>(undefined);

function CountriesProvider({ children }: CountriesProps) {
  const [state, setState] = useState<Context>({});

  useEffect(() => {
    getCountries().then((countries) => setState({ countries }));
  }, []);

  return (
    <CountriesContext.Provider value={state}>
      {children}
    </CountriesContext.Provider>
  );
}

function useCountries() {
  const context = useContext(CountriesContext);
  if (context !== undefined) return context;
  throw new Error('useCountries must be used within a CountriesProvider');
}

export { CountriesProvider, useCountries };
