import { Cart } from '@/checkout/data/models';
import { isBillToShippingSignal } from '@/checkout/signals/IsBillToShippingSignal';
import { effect, signal } from '@preact/signals';

export const cartSignal = signal<{
  pending: boolean;
  data?: Cart | null;
}>({ pending: false, data: undefined });

effect(() => {
  if (!cartSignal.value.data?.virtual) return;
  isBillToShippingSignal.value = { checked: false, setByUser: false };
});
