import { fetchAddressFormFields } from '@/checkout/api';
import { AddressFormField } from '@/checkout/data/models';
import { ComponentChildren, createContext } from 'preact';
import { useContext, useEffect, useState } from 'preact/hooks';

type Context = { fields?: AddressFormField[] };

type AddressFormFieldsProps = {
  children: ComponentChildren;
};

const AddressFormFieldsContext = createContext<Context | undefined>(undefined);

function AddressFormFieldsProvider({ children }: AddressFormFieldsProps) {
  const [state, setState] = useState<Context>({});

  useEffect(() => {
    fetchAddressFormFields().then((addressFormFields) =>
      setState({ fields: addressFormFields })
    );
  }, []);

  return (
    <AddressFormFieldsContext.Provider value={state}>
      {children}
    </AddressFormFieldsContext.Provider>
  );
}

function useAddressFormFields() {
  const context = useContext(AddressFormFieldsContext);
  if (context !== undefined) return context;
  throw new Error(
    'useAddressFormFields must be used within an AddressFormFieldsProvider'
  );
}

export { AddressFormFieldsProvider, useAddressFormFields };
