import {
  CustomerAttributeMetadata,
  FetchAddressFormFieldsQuery,
  ValidationRuleEnum,
} from '@/checkout/__generated__/types';
import { AddressFormField } from '@/checkout/data/models';

type AttributesForm = FetchAddressFormFieldsQuery['attributesForm'];
type AttributesFormFields = NonNullable<AttributesForm>['items'];

const transformOptions = (
  data: CustomerAttributeMetadata['options']
): AddressFormField['options'] => {
  if (!data) return [];

  const filteredOptions = data.filter(Boolean).filter((option) => {
    return option?.label && option?.value;
  }) as { label: string; value: string }[];

  return filteredOptions.map((option) => ({
    text: option.label,
    value: option.value,
  }));
};

const transformValidateRules = (
  data: CustomerAttributeMetadata['validate_rules']
): AddressFormField['validateRules'] => {
  if (!data) return [];

  return data.filter(Boolean).filter((rule) => {
    return rule?.name && rule?.value;
  }) as { name: ValidationRuleEnum; value: string }[];
};

const transformAddressFormFields = (
  data: AttributesFormFields
): AddressFormField[] => {
  if (!data) return [];

  const customerFields = data.filter(Boolean) as CustomerAttributeMetadata[];

  return customerFields.map((item) => {
    return {
      code: item.code,
      defaultValue: item.default_value || undefined,
      frontendInput: item.frontend_input || undefined,
      isDisabled: false,
      isRequired: item.is_required,
      label: item.label || undefined,
      multilineCount: item.multiline_count || undefined,
      options: transformOptions(item.options),
      sortOrder: item.sort_order || undefined,
      validateRules: transformValidateRules(item.validate_rules),
    };
  });
};

export { AttributesForm, transformAddressFormFields };
