import { Cart } from '@/checkout/data/models';
import { createContext, FunctionComponent } from 'preact';
import { Dispatch, Reducer, useContext, useReducer } from 'preact/hooks';

export enum CheckoutState {
  EMPTY = 'EMPTY',
  IN_PROGRESS = 'IN_PROGRESS',
  INIT = 'INIT',
  LOADING = 'LOADING',
  COMPLETE = 'COMPLETE',
}

export enum CheckoutEvent {
  CART_UPDATED,
  ORDER_PLACED,
  USER_AUTHENTICATED,
  USER_UNAUTHENTICATED,
  USER_SIGN_OUT,
}

type CartUpdated = {
  type: CheckoutEvent.CART_UPDATED;
  payload?: Cart | null;
};

type UserAuthenticated = {
  type: CheckoutEvent.USER_AUTHENTICATED;
};

type UserUnAuthenticated = {
  type: CheckoutEvent.USER_UNAUTHENTICATED;
};

type UserSignedOut = {
  type: CheckoutEvent.USER_SIGN_OUT;
};

type OrderPlaced = {
  type: CheckoutEvent.ORDER_PLACED;
};

type CheckoutAction =
  | CartUpdated
  | UserAuthenticated
  | UserUnAuthenticated
  | OrderPlaced
  | UserSignedOut;

const checkoutStateReducer: Reducer<CheckoutState, CheckoutAction> = (
  state,
  event
) => {
  const getStateBasedOnCart = (cart?: Cart | null) => {
    const isInitialized = cart !== undefined;
    const cartItems = cart?.items;
    const hasCartItems = cartItems !== undefined && cartItems.length > 0;
    if (!isInitialized) return CheckoutState.INIT;
    if (isInitialized && !hasCartItems) return CheckoutState.EMPTY;
    return CheckoutState.IN_PROGRESS;
  };

  let nextState = state;

  switch (state) {
    case CheckoutState.INIT:
      if (event.type === CheckoutEvent.CART_UPDATED)
        nextState = getStateBasedOnCart(event.payload);
      break;
    case CheckoutState.IN_PROGRESS:
      if (event.type === CheckoutEvent.ORDER_PLACED)
        nextState = CheckoutState.COMPLETE;
      if (event.type === CheckoutEvent.USER_AUTHENTICATED)
        nextState = CheckoutState.LOADING;
      if (event.type === CheckoutEvent.USER_SIGN_OUT)
        nextState = CheckoutState.LOADING;
      if (event.type === CheckoutEvent.CART_UPDATED)
        nextState = getStateBasedOnCart(event.payload);
      break;
    case CheckoutState.EMPTY:
    case CheckoutState.LOADING:
      if (event.type === CheckoutEvent.USER_UNAUTHENTICATED)
        nextState = CheckoutState.EMPTY;
      if (event.type === CheckoutEvent.CART_UPDATED)
        nextState = getStateBasedOnCart(event.payload);
  }

  return nextState;
};

type CheckoutContextValue = {
  state: CheckoutState;
  dispatch: Dispatch<CheckoutAction>;
};

export const CheckoutContext = createContext<CheckoutContextValue | undefined>(
  undefined
);

export const CheckoutStateProvider: FunctionComponent = ({ children }) => {
  const [state, dispatch] = useReducer(
    checkoutStateReducer,
    CheckoutState.INIT
  );

  const value = { state, dispatch };

  return (
    <CheckoutContext.Provider value={value}>
      {children}
    </CheckoutContext.Provider>
  );
};

export function useCheckoutState(): CheckoutContextValue {
  const context = useContext(CheckoutContext);
  if (context !== undefined) return context;
  throw new Error(
    'useCheckoutState must be used within a CheckoutStateProvider'
  );
}
