import { Country as CountryModel } from '@/checkout/data/models/country';
import { GetCountriesQuery } from '@/checkout/__generated__/types';

type AvailableCountries = NonNullable<GetCountriesQuery['countries']>;
type NonNullableCountry = NonNullable<AvailableCountries[number]>;
type FilteredCountry = {
  two_letter_abbreviation: NonNullable<
    NonNullableCountry['two_letter_abbreviation']
  >;
  full_name_locale: NonNullable<NonNullableCountry['full_name_locale']>;
};

export const transformCountries = (
  data: GetCountriesQuery['countries']
): CountryModel[] | undefined => {
  if (!data) return;

  return data
    .filter((country) => !!country)
    .filter((country) => {
      const { two_letter_abbreviation, full_name_locale } =
        country as NonNullableCountry;
      return !!two_letter_abbreviation && !!full_name_locale;
    })
    .map((country) => {
      const { two_letter_abbreviation, full_name_locale } =
        country as FilteredCountry;
      return { value: two_letter_abbreviation, label: full_name_locale };
    });
};
