import {
  GetStoreConfigQuery,
  TaxWrappingEnum,
} from '@/checkout/__generated__/types';
import { STORE_CONFIG_DEFAULTS } from '@/checkout/context';
import {
  PriceDisplay,
  StoreConfig,
  TotalDisplay,
} from '@/checkout/data/models';
import { isNullish } from '@/checkout/utils/isNullish';

function transformTotalDisplay(totalDisplay?: number | null) {
  if (totalDisplay === 0) return TotalDisplay.Rows;
  if (totalDisplay === 1) return TotalDisplay.Quantity;
  return TotalDisplay.Quantity;
}

function transformPriceDisplay(taxDisplay: number | null | undefined) {
  if (taxDisplay === 1) return PriceDisplay.ExcludingTax;
  if (taxDisplay === 2) return PriceDisplay.IncludingTax;
  if (taxDisplay === 3) return PriceDisplay.IncludingAndExcludingTax;
  return PriceDisplay.ExcludingTax;
}

function transformPriceDisplayGiftWrapping(taxDisplay: TaxWrappingEnum) {
  switch (taxDisplay) {
    case TaxWrappingEnum.DisplayExcludingTax:
      return PriceDisplay.ExcludingTax;
    case TaxWrappingEnum.DisplayIncludingTax:
      return PriceDisplay.IncludingTax;
    case TaxWrappingEnum.DisplayTypeBoth:
      return PriceDisplay.IncludingAndExcludingTax;
  }
}

export function transformStoreConfig(
  data: GetStoreConfigQuery['storeConfig']
): StoreConfig {
  if (!data) return STORE_CONFIG_DEFAULTS;

  const {
    default_country,
    countries_with_required_region,
    display_state_if_optional,
    optional_zip_countries,
    is_guest_checkout_enabled,
    is_one_page_checkout_enabled,
    shopping_cart_display_price,
    shopping_cart_display_shipping,
    shopping_cart_display_subtotal,
    shopping_cart_display_tax_gift_wrapping,
    shopping_cart_display_grand_total,
    shopping_cart_display_full_summary,
    shopping_cart_display_zero_tax,
    max_items_in_order_summary,
    cart_summary_display_quantity,
  } = data;

  return {
    defaultCountry: default_country || STORE_CONFIG_DEFAULTS.defaultCountry,
    countriesWithRequiredRegion:
      countries_with_required_region?.split(',') ||
      STORE_CONFIG_DEFAULTS.countriesWithRequiredRegion,
    displayStateIfOptional:
      display_state_if_optional || STORE_CONFIG_DEFAULTS.displayStateIfOptional,
    countriesWithOptionalZipCode:
      optional_zip_countries?.split(',') ||
      STORE_CONFIG_DEFAULTS.countriesWithOptionalZipCode,
    isGuestCheckoutEnabled:
      is_guest_checkout_enabled || STORE_CONFIG_DEFAULTS.isGuestCheckoutEnabled,
    isOnePageCheckoutEnabled:
      is_one_page_checkout_enabled ||
      STORE_CONFIG_DEFAULTS.isOnePageCheckoutEnabled,
    taxCartDisplay: {
      shoppingCartDisplayPrice: shopping_cart_display_price
        ? transformPriceDisplay(shopping_cart_display_price)
        : STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplayPrice,
      shoppingCartDisplayShipping: shopping_cart_display_shipping
        ? transformPriceDisplay(shopping_cart_display_shipping)
        : STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplayShipping,
      shoppingCartDisplaySubtotal: shopping_cart_display_subtotal
        ? transformPriceDisplay(shopping_cart_display_subtotal)
        : STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplaySubtotal,
      shoppingCartDisplayGiftWrapping: shopping_cart_display_tax_gift_wrapping
        ? transformPriceDisplayGiftWrapping(
            shopping_cart_display_tax_gift_wrapping
          )
        : STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplayGiftWrapping,
      shoppingCartDisplayGrandTotal:
        shopping_cart_display_grand_total ||
        STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplayGrandTotal,
      shoppingCartDisplayFullSummary:
        shopping_cart_display_full_summary ||
        STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplayFullSummary,
      shoppingCartDisplayZeroTax:
        shopping_cart_display_zero_tax ||
        STORE_CONFIG_DEFAULTS.taxCartDisplay.shoppingCartDisplayZeroTax,
    },
    cartSummaryMaxItems:
      max_items_in_order_summary || STORE_CONFIG_DEFAULTS.cartSummaryMaxItems,
    cartSummaryTotalDisplay: !isNullish(cart_summary_display_quantity)
      ? transformTotalDisplay(cart_summary_display_quantity)
      : STORE_CONFIG_DEFAULTS.cartSummaryTotalDisplay,
  };
}
