import { fetchGraphQl } from '@/checkout/api';
import { AddressFormField } from '@/checkout/data/models';
import { transformAddressFormFields } from '@/checkout/data/transforms';
import { fetchAddressFormFieldsQuery } from './graphql/fetchAddressFormFields.graphql';
import { handleFetchErrors } from '@/checkout/lib';
import { handleNetworkError } from '@/checkout/lib/network-error';
import { FetchAddressFormFieldsQuery } from '@/checkout/__generated__/types';

export const fetchAddressFormFields = async (): Promise<AddressFormField[]> => {
  return fetchGraphQl<FetchAddressFormFieldsQuery>(
    fetchAddressFormFieldsQuery,
    {
      method: 'GET',
      cache: 'no-cache',
    }
  )
    .then(({ data, errors }) => {
      handleFetchErrors(errors);
      return transformAddressFormFields(data.attributesForm.items);
    })
    .catch(handleNetworkError);
};
