import { GetStoreConfigQuery } from '@/checkout/__generated__/types';
import { fetchGraphQl } from '@/checkout/api/fetch-graphql';
import { StoreConfig } from '@/checkout/data/models';
import { transformStoreConfig } from '@/checkout/data/transforms';
import { handleFetchErrors } from '@/checkout/lib';
import { handleNetworkError } from '@/checkout/lib/network-error';
import { getStoreConfigQuery } from './graphql/getStoreConfig.graphql';

export const getStoreConfig = async (): Promise<StoreConfig> => {
  return fetchGraphQl<GetStoreConfigQuery>(getStoreConfigQuery, {
    method: 'GET',
    cache: 'no-cache',
  })
    .then(({ data, errors }) => {
      handleFetchErrors(errors);
      return transformStoreConfig(data?.storeConfig);
    })
    .catch(handleNetworkError);
};
