import { getCountriesQuery } from '@/checkout/api/getCountries/graphql/getCountries.graphql';
import { fetchGraphQl } from '@/checkout/api/fetch-graphql';
import { handleNetworkError } from '@/checkout/lib/network-error';
import { transformCountries } from '@/checkout/data/transforms/transform-countries';
import { Country as CountryModel } from '@/checkout/data/models/country';
import { handleFetchErrors } from '@/checkout/lib/fetch-error';
import { GetCountriesQuery } from '@/checkout/__generated__/types';

export const getCountries = async (): Promise<CountryModel[] | undefined> => {
  return fetchGraphQl<GetCountriesQuery>(getCountriesQuery, {
    method: 'GET',
    cache: 'no-cache',
  })
    .then(({ data, errors }) => {
      if (errors) handleFetchErrors(errors);
      return transformCountries(data.countries);
    })
    .catch(handleNetworkError);
};
